/*eslint-disable @typescript-eslint/no-unused-vars */

import React from "react"
import { MyForm, MyModal } from "src/core/components"
import { Button, Card, CardBody, CardHeader, CardText } from "reactstrap"
const configForm1 = {
  // defaultValues: {},//empty
  defaultValues: {
    fName: "Test fName",
    fListCheck: ["a"],
    fListRadio: ["b"],
    fSelect2: ["a"],
    fSelect2multi: ["a", "b"],
  },
  more: {
    positionButtonsBottom: "right",
    moreButtons: [
      {
        APIName: "a/b",
        Name: "api",
        Type: "api",
      },
      {
        Name: "link",
        Type: "link",
        OpenMode: "NewWindow",
        Url: "https://google.com",
      },
    ],
  },
  controls: [
    {
      label: "Name",
      fieldName: "fName",
      type: "text",
      more: {
        relativeTo: ["fName2"],
      },
    },
    {
      label: "Address",
      fieldName: "fAddress",
      type: "text",
      hint: "Input address here",
    },
    {
      label: "Address More",
      fieldName: "fAddressMore",
      type: "text",
      more: {
        conditionShow: {
          fAddress: "a",
        },
      },
    },
    {
      label: "Effect To 1",
      fieldName: "fEffectTo1",
      type: "text",
      more: {
        hint: "Input value here",
      },
    },
    {
      label: "Effect To 2",
      fieldName: "fEffectTo2",
      type: "text",
      more: {
        hint: "Input value here",
      },
    },
    {
      label: "Effected Sum",
      fieldName: "fEffectedSum",
      type: "text",
      more: {
        effectedBy: ["fEffectTo1", "fEffectTo2"],
        effectedValue: dataForm => {
          console.warn("calc effected Value:", dataForm)
          let f1 = dataForm["fEffectTo1"] + dataForm["fEffectTo2"]
          return f1
        },
        hint: "Value here depend Effect To",
      },
    },
    {
      label: "Email with check valid",
      fieldName: "fEmail",
      type: "text",
      more: {
        placeholder: "abc@company.com",
        hint: "Insert email here",
        rules: {
          required: true,
          minLength: {
            value: 3,
            msg: "Min length is 3",
          },
        },
      },
    },
  ],
  fnList: {},
}
const PageDev = () => {
  return (
    <button
      onClick={() => {
        MyModal.showFromMyForm(configForm1, {
          title: "Form in Modal",
          fnList: {
            onSubmitForm: (values, opts) => {
              console.warn("submit:", values, opts)
            },
          },
        })
      }}
    >
      Show form
    </button>
  )
}

export default PageDev
